<template>
  <div class="hot">
    <div class="loding" v-if="logding"></div>
    <div v-else>
      <HotList v-for="(item, index) in HotSong" :key="item.id" :item="item" :index="index"
        @hot-list="$emit('hot-list', $event, HotSong)" :song="song" :palybt="palybt" />
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import HotList from "@/components/HotList.vue";

export default {
  name: "SearchSong",
  props: {
    song: Object,
    palybt: Boolean,
  },
  components: {
    HotList,
  },
  data() {
    return {
      HotSong: [],
      logding: true,
    };
  },
  created() {
    this.logding = true;
    this.axios

      .get("https://apis.netstart.cn/music/playlist/detail", {
        params: { id: 3778678, limit: 20 },
      })
      .then((res) => {
        // console.log(res.data.playlist.tracks.splice(20, 200));
        this.logding = false;
        // this.HotSong = this.HotSong.splice(20);
        // console.log(this.HotSong.splice(20, 200));
        res.data.playlist.tracks.splice(20);
        this.HotSong = res.data.playlist.tracks;
        // console.log(res.data.playlist.tracks);
      });
  },
};
</script>
<style lang="less" scoped>
.hot {
  background-color: #fff;
  width: 100%;

  .loding {
    background-image: url("data:image/gif;base64,");
    background-repeat: no-repeat;
    background-position: center;
    width: 56vh;
    height: 50vh;
    margin: 0;
  }
}
</style>